import axios from "axios";

class TypstService {
  constructor() {
    // TypstURL
    this.baseURL =
      process.env.REACT_APP_TYPST_SERVER_URL || "http://localhost:8080";
    this.api = axios.create({
      baseURL: this.baseURL,
      timeout: 30000, // 30
    });
  }

  /**
   * TypstPNG
   * @param {string} source - Typst
   * @returns {Promise<{success: boolean, data?: {totalPages: number, pages: Array}, error?: string}>}
   */
  async compile(source) {
    try {
      const response = await this.api.post(
        "/compile",
        {
          source,
          format: "png",
        },
        {
          responseType: "json", // JSON
          headers: {
            "Content-Type": "application/json",
          },
        },
      );

      if (response.status === 200 && response.data.success) {
        return {
          success: true,
          data: response.data,
        };
      } else {
        return {
          success: false,
          error: response.data.error || "",
        };
      }
    } catch (error) {
      console.error("Typst:", error);

      if (error.response) {
        // 
        return {
          success: false,
          error:
            error.response.data?.message ||
            `: ${error.response.status}`,
        };
      } else if (error.request) {
        // 
        return {
          success: false,
          error: "Typst",
        };
      } else {
        // 
        return {
          success: false,
          error: error.message || "",
        };
      }
    }
  }

  /**
   * 
   * @returns {Promise<boolean>}
   */
  async checkHealth() {
    try {
      const response = await this.api.get("/health");
      return response.status === 200;
    } catch (error) {
      console.error(":", error);
      return false;
    }
  }

  /**
   * 
   * @returns {Promise<Array<string>>}
   */
  async getSupportedFeatures() {
    try {
      const response = await this.api.get("/features");
      return response.data.features || [];
    } catch (error) {
      console.error(":", error);
      return [];
    }
  }
}

// 
const typstService = new TypstService();

export default typstService;
